  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.29;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.reviews__button:hover {
  background-color: #0a8817;
  border-color: #0a8817;
}

.reviews__button:focus {
  color: #fcfcfc;
  background-color: #0a8817;
  border-color: #0a8817;
}

.reviews__button:disabled {
  background-color: #ababab;
  border-color: #ababab;
}

.reviews__button-agreement {
  background-color: inherit;
  color: #000;
  min-width: 196px;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 12px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.29;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.reviews__button-agreement:hover {
  border-color: #0a8817;
}

.reviews__button-agreement:focus {
  color: #fcfcfc;
  border-color: #0a8817;
}

.reviews__button-agreement:disabled {
  border-color: #ababab;
}

.reviews__button:focus .button-icon, .reviews__button-agreement:focus .button-icon {
  fill: #fcfcfc;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

